<template>
  <v-container fluid>
    <div class="text-center">
      <h1>Diagnostico Laboratorio Chincha</h1>
    </div>
    <v-row justify="center">
      <v-col cols="12" sm="6" md="4" class="text-center">
        <v-row justify="center" class="ma-2">
          <v-btn
            @click="listarSinResultado()"
            text
            :class=" listaSinResultados ? 'blue darken-3 white--text' : 'border-btn' ">
            Sin resultados
          </v-btn>
          <v-divider vertical></v-divider>
          <v-btn
            text
            @click="listarConResultado()"
            :class="
              !listaSinResultados ? 'blue darken-3 white--text' : 'border-btn'
            " >Con Resultados
          </v-btn>
        </v-row>

        <v-form
          ref="formResultadoChincha"
          lazy-validation
          @submit.prevent="Enviar"
          :disabled="cancelarInputResultado"
        >
          <h4 class="mb-2 red--text">
            Las filas rojas son resultados atrasados pendientes
          </h4>
          <h4 class="my-2" v-if="pacienteSeleccionado !== ''">
            {{ pacienteSeleccionado }}
          </h4>
          <template>
            <v-file-input
              label="Subir Archivo"
              :background-color="color_lighten_salud"
              v-model="archivo"
              outlined
              dense
              :rules="archivoRequerido"
              show-size
              counter
              :color="color_salud"
            ></v-file-input>
          </template>
          <v-select
            :items="arrayUrgencia"
            :item-color="color_salud"
            :class="color_text_salud"
            label="Nivel de Urgencia"
            :color="color_salud"
            :background-color="color_lighten_salud"
            item-text="nombre"
            item-value="valor"
            :rules="nivelRequerido"
            v-model="nivelUrgencia"
            dense
            outlined
          ></v-select>
          <v-textarea
            v-model="descripcion"
            :color="color_salud"
            label="Descripcion"
            dense
            outlined
            background-color="blue lighten-4"
            :no-resize="true"
          ></v-textarea>
          <div class="d-flex justify-space-between">
            <v-btn
              :disabled="cancelarInputResultado"
              type="submit"
              dark
              color="red darken-4"
            >
              Enviar</v-btn
            >
            <v-btn :disabled="cancelarInputResultado" dark @click="cancelar()">
              Cancelar</v-btn
            >
          </div>
        </v-form>
        <v-alert
          v-if="modificacionHecha"
          dense
          class="mt-4 text-center"
          type="success"
        > Registro Exitoso</v-alert>
        <v-alert
          v-if="modificacionConNuevoResultado"
          dense
          class="mt-4 text-center"
          type="success"
        >
          Modificaci√≥n Exitosa</v-alert
        >
      </v-col>

      <v-col>
        <div class="container-fluid">
          <v-row justify="center" class="pa-0 ma-0" v-if="!listaSinResultados">
            <v-col cols="12" md="5" class="pa-1">
              <v-text-field
                type="date"
                v-model="desde"
                color=" blue darken-2"
                label="Desde"
                dense
                outlined
                background-color="blue lighten-5"
              ></v-text-field>
            </v-col>
            <v-col cols="10" md="5" class="pa-1">
              <v-text-field
                type="date"
                v-model="hasta"
                color=" blue darken-2"
                label="Hasta"
                dense
                outlined
                background-color="blue lighten-5"
              ></v-text-field>
            </v-col>
            <v-col cols="2" md="2" class="ma-0 pa-1">
              <v-btn
                class="py-1"
                block
                dark
                color=" blue darken-4 "
                @click="listarConResultado"
              >
                <v-icon> search </v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </div>
           <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Buscar"
              single-line 
              hide-details
              class="mb-2"
            ></v-text-field>
        <v-data-table
          v-if="listaSinResultados"
          dense
          fixed-header
          :headers="dataHeaders"
          :items="dataTable"
          :search="search"
          class="elevation-1 table_main font-weight-medium"
          :loading="loading"
          loading-text="Cargando.. Espere porfavor"
          :item-class="pintarAtrasado"
        >
          <template v-slot:[`item.nombres`]="{ item }">
            {{ item.nombres }} {{ item.apellidos }}
          </template>
          <template v-slot:[`item.fechaAtencion`]="{ item }">
            {{ convertidorFecha(item.fechaAtencion) }}
          </template>
          <template v-slot:[`item.fechaEntregaResultado`]="{ item }">
            <td
              :class="{
                'red ':
                  restarFecha(convertidorFecha(item.fechaEntregaResultado)) <
                  -1,
              }"
            >
              {{ convertidorFecha(item.fechaEntregaResultado) }}
            </td>
          </template>
          <template v-slot:[`item.accion`]="{ item }">
            <v-btn
              @click="selectExamen(item)"
              elevation="3"
              icon
              outlined
              small
              class="ma-1"
            >
              <v-icon>edit</v-icon>
            </v-btn>
          </template>
        </v-data-table>



        <v-data-table
          v-if="!listaSinResultados"
          dense
          fixed-header
          :headers="dataHeadersEdit"
          :items="dataTable"
          :search="search"
          class="elevation-1 table_main font-weight-medium"
          :loading="loading"
          loading-text="Cargando.. Espere porfavor"
        >
          <template v-slot:[`item.nombres`]="{ item }">
            {{ item.nombres }} {{ item.apellidos }}
          </template>
          <template v-slot:[`item.fechaInforme`]="{ item }">
            {{ convertidorFecha(item.fechaInforme) }}
          </template>
          <template v-slot:[`item.fechaEntregaResultado`]="{ item }">
            <td>
              {{ convertidorFecha(item.fechaEntregaResultado ) }}
            </td>
          </template>
          <template v-slot:[`item.accion`]="{ item }">
            <v-btn
              @click="selectExamen(item)"
              elevation="3"
              icon
              outlined
              small
              class="ma-1"
            >
              <v-icon>edit</v-icon>
            </v-btn>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
    <v-dialog v-model="dialogSizeFile" max-width="290" persistent>
      <v-card class="py-2">
        <v-list-item-content class="text-center">
          <div class="font-weight-bold">
            El tama√±o del archivo excedio los 16 MB
          </div>
        </v-list-item-content>
        <v-list-item-content>
          <v-layout :justify-center="true">
            <v-btn @click="cerrarDialog()" rounded dark>Cerrar</v-btn>
          </v-layout>
        </v-list-item-content>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "DiagnosticoChincha",
  data() {
    return {
      search: '',
      desde: "",
      hasta: "",
      listaSinResultados: true,
      color_salud: "blue darken-3",
      color_lighten_salud: "blue lighten-4",
      color_lighten3_salud: "blue lighten-3",
      color_text_salud: "blue--text text-lighten-4",
      loading: true,
      cancelarInputResultado: true,
      modificacionHecha: false,
      modificacionConNuevoResultado: false,
      pacienteSeleccionado: "",
      mensajeError: "",
      descripcion: "",
      archivo: [],
      nivelUrgencia: "",
      idResultado: "",
      nivelRequerido: [(v) => !!v || "Se requiere Nivel"],
      archivoRequerido: [(v) => (!!v && v.size > 0) || "Se requiere Archivo"],
      arrayUrgencia: [
        { nombre: "Normal", valor: "N" },
        { nombre: "Media", valor: "M" },
        { nombre: "Alta", valor: "A" },
      ],
      dataTable: [],
      dataHeaders: [
        {
          text: "*",
          align: "start",
          sortable: false,
          value: "index",
          class:
            "headerStyleLeft blue darken-2 subtitle-1 text-center  font-weight-bold white--text ",
        },
        {
          text: "Dni",
          align: "center",
          value: "dni",
          sortable: false,
          class: " blue darken-2  subtitle-1   font-weight-bold white--text",
        },
        {
          text: "Paciente",
          align: "center",
          sortable: false,
          value: "nombres",
          class: "blue darken-2  subtitle-1  font-weight-bold white--text",
        },
        {
          text: "Examen",
          align: "center",
          value: "examen",
          class: " blue darken-2  subtitle-1   font-weight-bold white--text",
        },
        {
          text: "Atendido",
          align: "center",
          value: "fechaAtencion",
          class: " blue darken-2  subtitle-1  font-weight-bold white--text",
        },
        {
          text: "Entrega Resultado",
          align: "center",
          value: "fechaEntregaResultado",
          class: " blue darken-2 subtitle-1  font-weight-bold white--text",
        },
        {
          text: "",
          sortable: false,
          value: "accion",
          class:
            "headerStyleRigtht blue darken-2  font-weight-bold white--text",
        },
      ],dataHeadersEdit: [
        {
          text: "*",
          align: "start",
          sortable: false,
          value: "index",
          class:
            "headerStyleLeft blue darken-2 subtitle-1 text-center  font-weight-bold white--text ",
        },
        {
          text: "Dni",
          align: "center",
          value: "dni",
          sortable: false,
          class: " blue darken-2  subtitle-1   font-weight-bold white--text",
        },
        {
          text: "Paciente",
          align: "center",
          sortable: false,
          value: "nombres",
          class: "blue darken-2  subtitle-1  font-weight-bold white--text",
        },
        {
          text: "Examen",
          align: "center",
          value: "examen",
          class: " blue darken-2  subtitle-1   font-weight-bold white--text",
        },
          {
          text: "Entrega Resultado",
          align: "center",
          value: "fechaEntregaResultado",
          class: " blue darken-2  subtitle-1  font-weight-bold white--text",
        },
        {
          text: "Registro Resultado",
          align: "center",
          value: "fechaInforme",
          class: " blue darken-2 subtitle-1  font-weight-bold white--text",
        },
        {
          text: "",
          sortable: false,
          value: "accion",
          class:
            "headerStyleRigtht blue darken-2  font-weight-bold white--text",
        },
      ],
      search: "",
      idExamen: "",
      dialogSizeFile: false,
    };
  },
  async mounted() {
    await this.inicializarFechas();
    this.listarExamenes();
  },
  computed: {
    ...mapState(["token"]),
  },
  watch: {
    archivo: function (val) {
      if (val?.size > 1600000) {
        this.dialogSizeFile = true;
      }
    },
  },
  methods: {
    inicializarFechas() {
      var hoy = new Date();
      var a√±o = hoy.getFullYear();
      var mes = hoy.getMonth() + 1;
      mes = mes < 10 ? "0" + mes : mes;
      var dia = hoy.getDate();
      dia = dia < 10 ? "0" + dia : dia;

      this.hasta = a√±o + "-" + mes + "-" + dia;
      hoy.setDate(hoy.getDate() - 7);
      a√±o = hoy.getFullYear();
      mes = hoy.getMonth() + 1;
      mes = mes < 10 ? "0" + mes : mes;
      dia = hoy.getDate();
      dia = dia < 10 ? "0" + dia : dia;
      this.desde = a√±o + "-" + mes + "-" + dia;
    },
    listarSinResultado() {
      this.listaSinResultados = true;
      this.cancelar();
      this.listarExamenes();
    },
    listarConResultado() {
      this.listaSinResultados = false;
      this.cancelar();
      this.listarExamenes();
    },
    pintarAtrasado: function (item) {
      return this.restarFecha(
        this.convertidorFecha(item.fechaEntregaResultado)
      ) < -1
        ? "red"
        : "";
    },
    cerrarDialog() {
      this.dialogSizeFile = false;
      this.archivo = [];
    },
    Enviar() {
      var regResultado = this.$refs.formResultadoChincha.validate();
      if (regResultado) {
        this.cancelarInputResultado = true;
        let formData = new FormData();
        let filePDF = this.archivo;
        formData.append("file", filePDF, filePDF.name);
        formData.append("idExamen", this.idExamen);
        formData.append("fechaInforme", this.FechaActual());
        formData.append("horaInforme", this.HoraActual());
        formData.append("descripcion", this.descripcion);
        formData.append("nivelUrgencia", this.nivelUrgencia);
        formData.append("idResultado", this.idResultado);

        var headers = {
          "Content-Type": "application/json",
          token: this.token,
        };
        if (this.listaSinResultados) {
          this.axios
            .post("/guardarResultadoPDFLaboratorio", formData, { headers })
            .then((res) => {
              this.descripcion = "";
              this.archivo = [];
              this.nivelUrgencia = "";
              this.modificacionHecha = true;
              setTimeout(() => {
                this.modificacionHecha = false;
              }, 2000);
              this.pacienteSeleccionado = "";
              this.dataTable = [];
              this.listarExamenes();
            })
            .catch((err) => {
              this.mensajeError = err;
              setTimeout(() => {
                this.mensajeError = "";
              }, 2000);
              console.log(err);
            });
        } else {
          this.axios
            .post("/modificarResultadoPDFLaboratorio", formData, { headers })
            .then((res) => {
              this.descripcion = "";
              this.archivo = [];
              this.nivelUrgencia = "";
              this.modificacionConNuevoResultado = true;
              setTimeout(() => {
                this.modificacionConNuevoResultado = false;
              }, 2000);
              this.pacienteSeleccionado = "";
              this.dataTable = [];
              this.listarExamenes();
            })
            .catch((err) => {
              this.mensajeError = err;
              setTimeout(() => {
                this.mensajeError = "";
              }, 2000);
              console.log(err);
            });
        }
      }
    },
    FechaActual() {
      const hoy = new Date();
      var a√±o = hoy.getFullYear();
      var mes = hoy.getMonth() + 1;
      mes = mes < 10 ? "0" + mes : mes;
      var dia = hoy.getDate();
      dia = dia < 10 ? "0" + dia : dia;
      return a√±o + "-" + mes + "-" + dia;
    },
    HoraActual() {
      const hoy = new Date();
      var hora = hoy.getHours();
      var min = hoy.getMinutes();
      var seg = hoy.getSeconds();
      return hora + ":" + min + ":" + seg;
    },
    convertidorFecha(str) {
      if (str == null) {
        return "Sin Data";
      } else {
        const f = new Date(str);
        var a√±o = f.getFullYear();
        var mes = f.getMonth() + 1;
        mes = mes < 10 ? "0" + mes : mes;
        var dia = f.getDate();
        dia = dia < 10 ? "0" + dia : dia;
        return a√±o + "/" + mes + "/" + dia;
      }
    },
    restarFecha(e) {
      let fecha1 = new Date(e);
      let fecha2 = new Date();
      let resta = fecha1.getTime() - fecha2.getTime();
      return Math.round(resta / (1000 * 60 * 60 * 24));
    },
    listarExamenes() {
      var headers = {
        "Content-Type": "application/json",
        token: this.token,
      };
      if (this.listaSinResultados) {
        this.axios
          .get("/listarExamPendientesLabChincha", { headers })
          .then((res) => {
            this.loading = false;
            let index = 0;
            this.dataTable = res.data;
            this.dataTable.map((e) => {
              e.retraso = this.restarFecha(
                this.convertidorFecha(e.fechaEntregaResultado)
              );
              return e;
            });
            this.dataTable.sort(function (a, b) {
              if (a.retraso > b.retraso) {
                return 1;
              }
              if (a.retraso < b.retraso) {
                //como el resultado es menor que 0
                //a se coloca en un indice menor es decir primero que el b
                return -1;
              }
              return 0;
            });
            this.dataTable.map((e) => {
              index++;
              e.index = index;
              return e;
            });
          })
          .catch((err) => {
            this.loading = false;
            console.log(err);
          });
      } else {
        this.axios
          .post(
            "/listarExamEditarLabChincha",
            { desde: this.desde, hasta: this.hasta },
            { headers }
          )
          .then((res) => {
            this.loading = false;
            let index = 0;
            this.dataTable = res.data;
            this.dataTable.map((e) => {
              e.retraso = this.restarFecha(
                this.convertidorFecha(e.fechaEntregaResultado)
              );
              return e;
            });
            this.dataTable.sort(function (a, b) {
              if (a.retraso > b.retraso) {
                return 1;
              }
              if (a.retraso < b.retraso) {
                //como el resultado es menor que 0
                //a se coloca en un indice menor es decir primero que el b
                return -1;
              }
              return 0;
            });

            this.dataTable.map((e) => {
              index++;
              e.index = index;
              return e;
            });
          })
          .catch((err) => {
            this.loading = false;
            console.log(err);
          });
      }
    },
    selectExamen(e) {
      this.pacienteSeleccionado =
        e.nombres + " " + e.apellidos + "(" + e.examen + ")";
      this.idExamen = e.idExamen;
      this.cancelarInputResultado = false;
      this.idResultado = e.idResultado;
    },
    cancelar() {
      this.cancelarInputResultado = true;
      this.pacienteSeleccionado = "";
      this.idExamen = "";
      this.descripcion = "";
      this.archivo = [];
      this.nivelUrgencia = "";
    },
  },
};
</script>